import React from 'react';
import { graphql } from 'gatsby';
import { useIntl } from 'gatsby-plugin-intl';
import Img from 'gatsby-image';
import { FormattedMessage, Link } from 'gatsby-plugin-intl';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Query } from '../../graphql-types';
import { Separator } from '../components/separator';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import Card from 'react-bootstrap/Card';

const BrandsPage: React.FunctionComponent<{ data: Query }> = ({ data: { allAllorsBrand } }) => {
  const intl = useIntl();
  const brands = allAllorsBrand.nodes.sort((a, b) => a.name.localeCompare(b.name));

  return (
    <Layout>
      <SEO title={intl.formatMessage({ id: 'brands' })} description={intl.formatMessage({ id: 'brands-description' })} />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <Breadcrumb>
                    <Breadcrumb.Item>
                      <Link to="/">Home</Link>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item active>
                      <FormattedMessage id="brands" />
                    </Breadcrumb.Item>
                  </Breadcrumb>
                  <h1 className="page-title">
                    <FormattedMessage id="brands" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="brands-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>

          <Container>
            <Row>
              <Col xl="9" lg={{ span: 8, order: 2 }} xs="12">
                <Row>
                  {brands &&
                    brands.map((brand) => {
                      const fluid = brand.logoImage?.file.childImageSharp.fluid;
                      return (
                        <Col key={brand.slug} md="4" xs="6">
                          <Link to={'/' + brand.slug} key={brand.slug} className="product-link">
                            <Card>
                              {fluid && <Img fluid={fluid} />}
                              <Card.Body>
                                <Card.Title>{brand.name}</Card.Title>
                              </Card.Body>
                            </Card>
                          </Link>
                        </Col>
                      );
                    })}
                </Row>
              </Col>
              <Col xl="3" lg={{ span: 4, order: 1 }} xs="12">
                <div className="list-group mb-4">
                  <Link to="/brands" className="list-group-item list-group-item-action active">
                    <FormattedMessage id="brands" />
                  </Link>
                  {brands &&
                    brands.map((brand) => {
                      return (
                        <Link key={`/${brand.slug}`} to={'/' + brand.slug} className="list-group-item list-group-item-action">
                          {brand.name}
                        </Link>
                      );
                    })}
                </div>
              </Col>
            </Row>
          </Container>
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    allAllorsBrand {
      nodes {
        name
        slug
        logoImage {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        partsWhereBrand {
          UnifiedGood {
            name
          }
        }
      }
    }
  }
`;

export default BrandsPage;
